var Airtable = require('airtable')
// read-only API key from rhizomaticode
var base = new Airtable({ apiKey: 'keyRHmFMa5W4S4TUJ' }).base('app1AzaEIEVOFm3nN')
module.exports = store

function store(state, emitter) {
  state.links = []

  state.tags = []

  window.addEventListener('resize', () => {
    emitter.emit('render')
  })

  emitter.on('navigate', () => {
    console.log(`Navigated to ${state.route}`)
  })

  emitter.on('toggle tag', (tag) => {
    const index = state.tags.indexOf(tag)
    if (index > -1) {
      state.tags.splice(index, 1)
    } else {
      state.tags.push(tag)
    }
    emitter.emit('render')
  })

  base('Links').select({
    // Selecting the first 50 records in Grid view:
    // maxRecords: 1000,
    pageSize: 100,
    view: "Grid view"
  }).eachPage(function page(records, fetchNextPage) {
    state.links = state.links.concat(records.map((record) => record.fields))
    console.log('records', records, state.links)

    fetchNextPage()
    emitter.emit(state.events.RENDER)
  }, function done(err) {
    if (err) { console.error(err); return; }
  })

  //   const DATA_URL = `${window.location.origin}/json`
  //   fetch(DATA_URL)
  //   .then(response => response.json())
  //   .then(data => {
  //     state.links = data 
  //     console.log(state.links)
  //     emitter.emit(state.events.RENDER)
  //   });
}
